  .blob {
    position: absolute;
    top: 0;
    left: 0;
    fill: #023F92;
    width: 50vmax;
    z-index: -1;
    -webkit-animation: move 10s ease-in-out infinite;
            animation: move 10s ease-in-out infinite;
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
  
  @-webkit-keyframes move {
    0%   { -webkit-transform: scale(1)   translate(10px, -30px); transform: scale(1)   translate(10px, -30px); }
    38%  { -webkit-transform: scale(0.8, 1) translate(80vw, 30vh) rotate(160deg); transform: scale(0.8, 1) translate(80vw, 30vh) rotate(160deg); }
    40%  { -webkit-transform: scale(0.8, 1) translate(80vw, 30vh) rotate(160deg); transform: scale(0.8, 1) translate(80vw, 30vh) rotate(160deg); }
    78%  { -webkit-transform: scale(1.3) translate(0vw, 50vh) rotate(-20deg); transform: scale(1.3) translate(0vw, 50vh) rotate(-20deg); }
    80%  { -webkit-transform: scale(1.3) translate(0vw, 50vh) rotate(-20deg); transform: scale(1.3) translate(0vw, 50vh) rotate(-20deg); }
    100% { -webkit-transform: scale(1)   translate(10px, -30px); transform: scale(1)   translate(10px, -30px); }
  }
  
  @keyframes move {
    0%   { -webkit-transform: scale(1)   translate(10px, -30px); transform: scale(1)   translate(10px, -30px); }
    38%  { -webkit-transform: scale(0.8, 1) translate(80vw, 30vh) rotate(160deg); transform: scale(0.8, 1) translate(80vw, 30vh) rotate(160deg); }
    40%  { -webkit-transform: scale(0.8, 1) translate(80vw, 30vh) rotate(160deg); transform: scale(0.8, 1) translate(80vw, 30vh) rotate(160deg); }
    78%  { -webkit-transform: scale(1.3) translate(0vw, 50vh) rotate(-20deg); transform: scale(1.3) translate(0vw, 50vh) rotate(-20deg); }
    80%  { -webkit-transform: scale(1.3) translate(0vw, 50vh) rotate(-20deg); transform: scale(1.3) translate(0vw, 50vh) rotate(-20deg); }
    100% { -webkit-transform: scale(1)   translate(10px, -30px); transform: scale(1)   translate(10px, -30px); }
  }
